import React, { useState, useEffect, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button, Navbar, NavbarToggler, Row } from 'reactstrap';

import logocompleto from '../../assets/img/logo-completo.png';
import { TOKEN_KEY } from '../../constants/auth';
import { AppContext } from '../../contexts/app';
import { useAuth } from '../../hooks/auth';

function Header() {
  const [token, setToken] = useState('');
  const { isAuthenticated, loggedUser } = useAuth();
  const navigation = useNavigate();
  const { setNavbarTogglerOpen } = useContext(AppContext);

  useEffect(() => {
    setToken(localStorage.getItem(TOKEN_KEY));
  }, []);

  return (
    <>
      <Row className="px-2 py-3 px-sm-5 align-items-center justify-content-center justify-content-sm-between">
        <Row className="justify-content-between">
          <img src={logocompleto} alt="" />
          <Navbar light className="d-lg-none">
            <NavbarToggler
              onClick={() => setNavbarTogglerOpen((prevState) => !prevState)}
            />
          </Navbar>
        </Row>
        <Row>
          {isAuthenticated ? (
            <a
              href={`${process.env.REACT_APP_ADMIN_USER_URL}/${token}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Button className="p-2 d-none d-sm-inline" color="link">
                Bem-Vindo
                {' '}
                {loggedUser.name}
                <i className="fas fa-user p-1" />
              </Button>
            </a>
          ) : (
            <Button
              className="p-2 d-none d-sm-inline"
              color="link"
              onClick={() => navigation('/login')}
            >
              Acessar Conta
              <i className="fas fa-user-cog p-1" />
            </Button>
          )}
          <a href="#anuncios">
            <Button className="m-2" outline>
              Quero Comprar
            </Button>
          </a>
          {isAuthenticated ? (
            <a
              href={`${process.env.REACT_APP_ADMIN_USER_URL}/${token}`}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Button
                color="primary"
                className="m-2"
              >
                Quero Anunciar
              </Button>
            </a>
          ) : (
            <Button
              color="primary"
              className="m-2"
              onClick={() => navigation('cadastro')}
            >
              Quero Anunciar
            </Button>
          )}
        </Row>
      </Row>
    </>
  );
}

export default Header;
